body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #333333;
}

html, body {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: fit-content;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  min-width: 300px;
  overflow-x: hidden;
  overflow-y: auto;
  user-select: none;
}

#root {
  width: 100%;
  height: fit-content;
  min-height: 100%;
}

img {
  user-select: none;
}